import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from "./App";
import msg from "./utils/showMsg";

import Vue from "vue";
Vue.config.productionTip = false;
App.mpType = "app";

try {
    function isPromise(obj) {
        return (
            !!obj &&
            (typeof obj === "object" || typeof obj === "function") &&
            typeof obj.then === "function"
        );
    }
    uni.$msg = msg;

    // 统一 vue2 API Promise 化返回格式与 vue3 保持一致
    uni.addInterceptor({
        returnValue(res) {
            if (!isPromise(res)) {
                return res;
            }
            return new Promise((resolve, reject) => {
                res.then((res) => {
                    if (res[0]) {
                        reject(res[0]);
                    } else {
                        resolve(res[1]);
                    }
                });
            });
        },
    });
} catch (error) {}

const app = new Vue({
    ...App,
});
app.$mount();